import { gsap } from 'gsap';
import router from '../router';
export default {
  name: 'NavMenu',
  data: function data() {
    return {
      isActive: false
    };
  },
  methods: {
    login: function login() {
      router.push('login');
      this.close();
    },
    register: function register() {
      router.push('register');
      this.close();
    },
    toggle: function toggle() {
      this.isActive ? this.close() : this.open();
    },
    open: function open() {
      this.isActive = true;
      gsap.to('#sidemenu-overlay', {
        opacity: 0.75,
        duration: 0.75
      });
    },
    close: function close() {
      var _this = this;

      gsap.to('#sidemenu-overlay', {
        opacity: 0,
        duration: 0.5
      }).then(function () {
        _this.isActive = false;
      });
    }
  }
};